
















































































































import util from '../../common/util'
import cityList from '../../common/cityList'
export default {
	data() {
		return {
			statusColor:'#1e2535',
			showLeftButton:true,
			showRightButton:true,
			titleClass:'',
			autoplay:true,
			interval:3000,
			indicatorDots:true,
			circular:true,
			duration:500,
			title: 'Hello',
			city:'深圳',
			cityList:[],
			cityCode:366,				
			navList:[
				{
					id:1,
					name:'推荐'
				},
				{
					id:2,
					name:'视频'
				},
				{
					id:3,
					name:'新闻'
				},
				{
					id:4,
					name:'预告片'
				},
				{
					id:5,
					name:'影评'
				},
				{
					id:6,
					name:'时光号'
				},
				{
					id:7,
					name:'票房'
				}
			],
			hotTicket:{
				movies:[
					{},
					{},
					{},
					{}
				]
			},
			commingMovies:{
				attention:[
					{},
					{},
					{},
					{}
				]
			}
			
		}
	},
	onLoad() {
		

	},
	onReady(){
		let self = this;
		self.init();
	},
	onPullDownRefresh(){
		let self = this;
		self.onPullDownRefresh();
	},
	methods: {
		tabClick(){
			console.log('click');
		},
		movieDetail(item){
			let movieId = item.id?item.id:item.movieId;
			let self = this;
			uni.navigateTo({
				url:'/pages/movies/movie-detail?movieId='+movieId+'&cityCode='+self.cityCode
			})
		},
		backButton(){

		},
		rightButton(){

		},
		onPullDownRefresh() {
			let self = this;
			self.init().then(()=>{
	            uni.stopPullDownRefresh();					
			});
	    },
	    init(){
			const self = this;	
			return new Promise((resolve,rejected)=>{
				self.cityList = cityList.data;	
				
				//定位
				uni.getLocation({
				    type: 'wgs84',
				    success: function (res) {
				    	//坐标转换 wgs84 => bd09 
				    	var cjo2 = util.wgs84togcj02(res.longitude,res.latitude);
				    	var bd09 = util.gcj02tobd09(cjo2[0],cjo2[1]);

				    	//获取当前城市信息
				      	util.getCityInfo(bd09[0],bd09[1]).then(res=>{
				      		self.cityList.forEach(function(item,index){
								if(self.city.indexOf(item.name) != -1){
									self.city = item.name;
									self.cityCode == item.id;	
								}
							})
				      	});
				    }
				});

				//热映购票，包含未上映
				util.request({
					url:'https://api-m.mtime.cn/PageSubArea/HotPlayMovies.api?locationId='+self.cityCode
				}).then(res=>{
					self.hotTicket = res;
				});

				//即将上映
				//attention 首页
				//moviecomings 所有即将上映影片
				util.request({
					url:'https://api-m.mtime.cn/Movie/MovieComingNew.api?locationId='+self.cityCode
				}).then(res=>{
					self.commingMovies = res;
					resolve();
				}).catch(rej=>{
					rejected();
				})			
			})
	    }
	},
	components:{
	}
}
